<template>
  <div class="box">
    <div class="image-download">
      <div class="image-item" v-for="(item) in imgList" :key="item.id">
        <span>{{ item.name }}</span>
        <img :src="item.url">
        <button @click="download(item.downloadURL,item.name)">下载</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'StorePage',
  computed: {
    ...mapState('store', ['imgList'])
  },
  methods: {
    download (url, filename) {
      const link = document.createElement('a')
      link.href = url
      // 设置下载的文件名
      link.download = `${filename}.txt` // 替换为你的文件名
      // 触发点击事件
      document.body.appendChild(link)
      link.click()
      // 移除元素
      document.body.removeChild(link)
    }
  }
}
</script>

<style scoped lang="less">
.box{
  max-width: 1000px;

  .image-download{
    text-align: center;
    padding: 50px 50px;
    .image-item{
      margin: 0 0 50px;
    }
    img{
      width: 100%;
      height: 100%;
      border-radius: 10px;
      margin-top: 10px;
    }
    button{
      margin-top: 20px;
      width: 200px;
      height: 60px;
      size: large;
      font-size: 17px;
      font-weight: bold;
      background-color: #42B983;
      border: 0;
      border-radius: 10px;
    }
    span{
      font-size: 20px;
      font-weight: bolder;
    }
  }

}
</style>
